import { computed, defineComponent, onMounted } from 'vue';
import { useStore } from '@/store';
import { useRouter } from 'vue-router';
import { regesterConfig } from './config/regester';
import pageForm from '@/components/page-form';
import { mapName } from '@/utils';
export default defineComponent({
  name: 'regester',
  components: {
    pageForm: pageForm
  },
  setup: function setup() {
    var router = useRouter();
    var store = useStore();
    onMounted(function () {
      store.dispatch('roleModule/queryRoleAction');
      store.dispatch('departmentModule/queryDepartmentAction');
    });
    var roleList = computed(function () {
      return store.state.roleModule.list;
    });
    var departmentList = computed(function () {
      return store.state.departmentModule.list;
    });

    var handleVisibleChange = function handleVisibleChange(item) {
      if (item.field === 'roleId') {
        mapName.visible(roleList.value, regesterConfig, 'roleId', 'id', 'name', item);
      }

      if (item.field === 'departmentId') {
        mapName.visible(departmentList.value, regesterConfig, 'departmentId', 'id', 'name', item);
      }
    };

    var handleReSearch = function handleReSearch(formdata) {
      store.dispatch('userModule/regesterUserAction', formdata);
      router.push('/main');
    };

    return {
      regesterConfig: regesterConfig,
      handleVisibleChange: handleVisibleChange,
      handleReSearch: handleReSearch
    };
  }
});